.container-blog {
	display: flex;
	flex-direction: row;
	text-align: center;
	justify-content: center;
}
.entry-title {
	margin-bottom: 0;
}

.date {
	margin: 0;
	margin-bottom: -10px;
	color: $typeAccent;
}

.blog-title {
	font-size: 1.8rem;
	font-style: normal;
	text-decoration: none;
}

.categories,
.expand-read {
	color: $typeAccent;
	font-weight: 400;
}

.expand-read {
	margin-left: 5px;
}

.organize-tags-cats {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 5px;
}

.blog-post-image {
	transition: opacity 0.3s;
	&:hover {
		opacity: 0.85;
	}
}

//next and previous buttons
